.navbar {
    display: flex;
    justify-content: space-between; /* Space between left and right content */
    align-items: center;
    background-color: #284B63;
    color: white;
    padding: 10px 75px;
    position: relative;
}

.name {
    font-size: 1.6em;
    font-weight: bold;
    letter-spacing: 3px;
}

.job-title {
    margin-left: 25px;
    color: #F4F4F4;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 15px;
}

.icon-links {
    display: flex;
    gap: 50px;
}

.icon {
    width: 40px;
    height: 40px;
    cursor: pointer;
    transition: transform 0.2s ease, opacity 0.2s ease;
    filter: invert(100%) brightness(1000%) contrast(50%);
}

.icon:hover {
    transform: scale(1.1);
    opacity: 0.8;
}

.tooltip {
    position: absolute;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%);
    background-color: black;
    color: white;
    padding: 5px;
    border-radius: 4px;
    font-size: 12px;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.2s, opacity 0.2s;
}

.icon-wrapper {
    position: relative;
}

.icon-wrapper:hover .tooltip {
    visibility: visible;
    opacity: 1;
}

@media (max-width: 1000px) {
    .navbar {
        padding: 10px 25px;
    }

    .job-title {
        margin-left: 15px;
    }

    .icon-links {
        gap: 10px;
    }
}

@media (max-width: 600px) {
  .navbar {
    padding: 8px 10px;
    gap: 8px;
  }
  .name {
    font-size: 0.8em;
    letter-spacing: 2px;
  }
  .job-title {
    font-size: 0.6em;
    margin-left: 8px;
  }
  .icon-links {
    gap: 16px;
  }
  .icon {
    width: 25px;
    height: 25px;
  }
  .nav-links {
    gap: 8px;
  }
}
